import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import {  getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class LaporanPenjualanMarketPlaceExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let stock_on_hand = 0;
    let berat = 0;
    let berat_atribut = 0;
    let total_barang = 0;
    let harga_jual = 0;
    let harga_atribut = 0;
    let berat_selisih = 0;
    let harga_gram = 0;
    let harga_total = 0;
    let ongkos = 0;
    this.props.data.forEach((item, index) => {
    
      // barang.detail.forEach((item, index) => {
        stock_on_hand = stock_on_hand + item.stock_on_hand;
        berat_atribut = berat_atribut + item.berat_atribut;
        berat = berat + item.berat;
        harga_jual = harga_jual + item.harga_jual;
        ongkos = ongkos + item.ongkos;
        harga_total = harga_total + item.harga_total;
        harga_gram = harga_gram + item.harga_gram;
        harga_atribut = harga_atribut + item.harga_atribut;
        berat_selisih = berat_selisih + item.berat_selisih;
      // });
      total_barang = total_barang + this.props.data.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Penjualan Marketplace"
          sheet="Laporan Penjualan Marketplace"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="7" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN TRANSAKSI PENJUALAN{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="7"> Periode </th>
            </tr>
            <tr>
              <th colSpan="7">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="7"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="7"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="7"></th>
            </tr>
            <tr>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NO FJ
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA MEMBER
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE JENIS
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA BARANG
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BERAT
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                ONGKOS
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                HARGA JUAL
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                HARGA TOTAL
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                MARLETPLACE
              </td>
             
              
            </tr>
          </thead>
          <tbody>
          {this.props.data.map((row, index) => (
                  <tr key={index}>
                    <td>
                      {row.no_faktur_jual}
                    </td>
                    <td>
                      {row.nama_customer}
                    </td>
                    <td>
                      {row.kode_dept}
                    </td>
                    <td>
                      {row.nama_barang}
                    </td>
                    <td style={{textAlign:"right"}}>
                      {row.berat.toFixed(2)}
                    </td>
                    <td style={{textAlign:"right"}}>
                    &nbsp;{row.ongkos.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign:"right"}}>
                    &nbsp;{row.harga_jual.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign:"right"}}>
                      &nbsp;{row.harga_total.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign:"right"}}>
                    &nbsp;{row.kode_marketplace}
                    </td>
                  </tr>
          ))}
          </tbody>
          <tfoot>
          <tr>
            <td colSpan="4" style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign :"center" }}>
              Total Transaksi Penjualan : {total_barang} 
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
              {berat.toFixed(2)}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000" , textAlign: "right"}}>
            &nbsp;{ongkos.toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000" , textAlign: "right"}}>
            &nbsp;{harga_jual.toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000" , textAlign: "right"}}>
            &nbsp;{harga_total.toLocaleString("kr-KO")}
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000" , textAlign: "right"}}>
            </td>
         
            </tr>
            </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanPenjualanMarketPlaceExel;
