import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate, ReanderSelect } from "../../../../components/helpers/field";
import {
  formatGram,
  getTglSystem,
} from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
// import LaporanExelPenjualan from "./exel/LaporanExelPenjualan";
// import LaporanBatalServiceExel from "./exel/LaporanBatalServiceExel";
// import ExportTransaksiPenjualan from "./pdf/ExportTransaksiPenjualan";
import Loading from "react-fullscreen-loading";
import { getDataMarketplace } from "../../../../actions/datamaster_action";
import ExportTransaksiPenjualanMarketPlace from "./pdf/ExportTransaksiPenjualanMarketPlace";
import LaporanPenjualanMarketPlaceExel from "./exel/LaporanPenjualanMarketPlaceExel";
class HeadLaporanPenjualanMkc extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      type_laporan: "SEMUA",
      databarang: [],
      columns : [
        {
          dataField: "no_faktur_jual",
          text: "No FJ",
          footerAttrs: {
            colSpan: "4",
          },
          footer: (cell, row, num, index) => {
            return <div>Total Barang : {this.props.LaporanDataPenjualan.length} </div>;
          },
        },
        // {
        //   dataField: "kode_member",
        //   text: "Kode Member",
        // },
        {
          dataField: "nama_customer",
          text: "Nama Member",
        },
        {
          dataField: "kode_dept",
          text: "Kode Jenis",
        },
        // {
        //   dataField: "kode_barcode",
        //   text: "Kode Barcode",
        // },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "berat",
          text: "Berat",
          headerClasses: "text-right",
          footer: (cell, row, num, index) => {
            return (
              <div className="text-right">
                {cell.reduce((a, b) => a + b, 0).toFixed(2)}
              </div>
            );
          },
          formatter: (cell) => {
            return <div className="text-right">{cell.toFixed(2)}</div>;
          },
        },
        {
          dataField: "ongkos",
          text: "Ongkos",
          headerClasses: "text-right",
          footer: (cell, row, num, index) => {
            return (
              <div className="text-right">
                {cell.reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}
              </div>
            );
          },
          formatter: (cell) => {
            return (
              <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
            );
          },
        },
        {
          dataField: "harga_jual",
          text: "Harga Jual",
          headerClasses: "text-right",
          footer: (cell, row, num, index) => {
            return (
              <div className="text-right">
                {cell.reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}
              </div>
            );
          },
          formatter: (cell, row) => {
            return (
              <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
            );
          },
        },
        {
          dataField: "harga_total",
          text: "Harga Total",
          headerClasses: "text-right",
          footer: (cell, row, num, index) => {
            return (
              <div className="text-right">
                {cell.reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}
              </div>
            );
          },
          formatter: (cell) => {
            return (
              <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
            );
          },
        },
        {
          dataField: "kode_marketplace",
          text: "Marketplace",
        },
      ]
    };
    // this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  componentDidMount() {
    this.props.change("type", "SEMUA");
    this.props.change("marketplace", "SEMUA");
    this.props.dispatch(getDataMarketplace());
  }

  exportPdf() {
    ExportTransaksiPenjualanMarketPlace(
      this.props.LaporanDataPenjualan,
      this.state.type_laporan
    );
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  typeLaporan(e) {
    if (e === "SEMUA") {
      this.setState({
        type_laporan: "SEMUA",
      });
    } else {
      this.setState({
        type_laporan: "SELISIH",
      });
    }
  }

  render() {
    let marketplace = [
      {
        value: "SEMUA",
        name: "SEMUA",
      },
    ];
    this.props.getDataMarketplace.forEach((list) => {
      let data = {
        value: list.kode_marketplace,
        name: list.kode_marketplace + " - " + list.nama_marketplace,
      };
      marketplace.push(data);
    });

    
    
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              Berat Awal : {formatGram(row.berat_awal, 3)} <br />
              Berat Selisih : {formatGram(row.berat_selisih, 3)} <br />
              Nama Atribut : {row.nama_atribut} <br />
              Berat Atribut : {row.berat_atribut} <br />
              Harga Atribut : {row.harga_atribut.toLocaleString("kr-KO")} <br />
              Ongkos : {row.ongkos.toLocaleString("kr-KO")} <br />
              Harga/Gram : {row.harga_gram.toLocaleString("kr-KO")} <br />
              Nama Member : {row.nama_customer} <br />
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-3">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-3">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
          <div className="col-lg-3">
            <Field
              id="marketplace"
              label="Marketplace"
              name="marketplace"
              options={marketplace}
              placeholder={"Silahkan Pilih Marketplace"}
              component={ReanderSelect}
            />
          </div>

          <div className="col-lg-3">
            <div className="form-group">
              <label>&nbsp;</label>
              <br />
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_barcode"
              data={this.props.export ? this.props.LaporanDataPenjualan : []}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Laporan Transaksi Penjualan Kosong"
              expandRow={expandRow}
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanPenjualanMarketPlaceExel
                  data={this.props.LaporanDataPenjualan}
                />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadLaporanPenjualanMkc = reduxForm({
  form: "HeadLaporanPenjualanMkc",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadLaporanPenjualanMkc);
export default connect((state) => {
  return {
    getDataMarketplace: state.datamaster.getDataMarketplace,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLaporanPenjualanMkc);
