import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const ExportTransaksiPenjualanMarketPlace = (
  data = "",
  type_laporan = "",
  tanggal_dari = "",
  tanggal_sampai = ""
) => {
  // console.log(data);
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  // let footRows = [];
  let finalY = 30;
  let total_berat_footer = 0;
  let total_harga_total_footer =0
  let total_baraang = 0
  let total_ongkos = 0
  let total_harga_jual = 0
  
  
  let jml_alamat = getUserdata().alamat_toko.length;
  doc.setFontSize(15);
  doc.text("LAPORAN PENJUALAN MARKETPLACE", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN PENJUALAN MARKETPLACE",
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  let tableColumnSemua = [
    // [
    //   {
    //     content: `No Faktur Group : ${item._id.no_faktur_group}`,
    //     colSpan: 10,
    //   },
    // ],
    [
      {
        content: `NO FJ`,
      },
      {
        content: `NAMA MEMBER`,
      },
      {
        content: `KODE JENIS`,
      },
      {
        content: `NAMA BARANG`,
      },
      {
        content: `BERAT`,
        styles: {
          halign: 'right',
        },
      },
     
      {
        content: `Ongkos`,
        styles: {
          halign: 'right',
        },
      },
      {
        content: `HARGA JUAL`,
        styles: {
          halign: 'right',
        },
      },
      {
        content: `HARGA TOTAL`,
        styles: {
          halign: 'right',
        },
      },
      {
        content: `MARKETPLACE`,
        styles: {
          halign: 'right',
        },
      },
    ],
  ];
  
 
  
  data.forEach((item, index) => {

    // let data = [
    //   {
    //     content: `No Faktur Group : ${item._id.no_faktur_group}`,
    //     colSpan: "10",
    //   },
    // ];
    // tableRows.push(data);

    // item.pembayaran.forEach((data_pembayaran, index) => {
    //   // console.log(data_pembayaran.jumlah_in);
     
    // })
   
    // item.detail.forEach((item, index) => {
     
      let rows = [
        item.no_faktur_jual,
        // item.kode_barcode,
        // item.kode_member,
        item.nama_customer,
        item.kode_dept,
        item.nama_barang,
        // formatGram(item.berat_awal, 3),
        {
          content: item.berat.toFixed(2),
          styles: {
            halign: 'right',
            textColor: "#000",
          },
        },
       
        {
          content:    parseInt(item.ongkos).toLocaleString("kr-KO"),
          styles: {
            halign: 'right',
            textColor: "#000",
          },
        },
        {
          content:  parseInt(item.harga_jual).toLocaleString("kr-KO"),
          styles: {
            halign: 'right',
            textColor: "#000",
          },
        },
        {
          content:  parseInt(item.harga_total).toLocaleString("kr-KO"),
          styles: {
            halign: 'right',
            textColor: "#000",
          },
        },
        {
          content:item.kode_marketplace,
          styles: {
            halign: 'right',
            textColor: "#000",
          },
        },
      
      ];

      total_harga_jual = total_harga_jual + parseInt(item.harga_jual);
      total_ongkos = total_ongkos + parseInt(item.ongkos);
      total_berat_footer = total_berat_footer + parseFloat(item.berat);
      total_harga_total_footer = total_harga_total_footer + parseInt(item.harga_total);
    
      tableRows.push(rows);
    // });
    total_baraang = total_baraang + data.length;
    
  });

  
  
  let footerSemua = [
    {
      content: `Total Transaksi Penjualan : ${total_baraang}`,
      colSpan: 4,
      styles: {
        halign: 'center',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${total_berat_footer.toFixed(2)}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${total_ongkos.toLocaleString("kr-KO")}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${total_harga_jual.toLocaleString("kr-KO")}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${parseInt(total_harga_total_footer).toLocaleString("kr-KO")}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: ``,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
  ];
  
  tableRows.push(footerSemua);
  

  let printed = [
    {
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      colSpan: 13,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
  
  doc.autoTable({
    head:  tableColumnSemua,
    body: tableRows,
    // foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    footStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      textColor: "#000",
      fillColor: "#E8E5E5",
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  // footRows = [];
  // tot_berat = 0;
  // tot_onkos = 0;
  // tot_harga_jual = 0;
  // tot_harga_gram = 0;
  // tot_berat_atribut = 0;
  // tot_harga_atribut = 0;
  // tot_harga_total = 0;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  // doc.text(`User                           : ${JSON.parse(localStoragedecryp("userdata")).user_id}`, 14, finalY + 10);
  // doc.text(`Cetak                        : ${getToday()}`, 14, finalY + 16);
  // doc.text(`Total Barang Terjual : ${total_barang}`, 14, finalY + 20);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  doc.save(`LAPORAN PENJUALAN MARKETPLACE.pdf`);
  // var string = doc.output("datauristring");
  // // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  // var x = window.open();
  // x.document.open();
  // x.document.write(
  //   "<html><head><title>LAPORAN PENJUALAN MARKETPLACE</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
  //     string +
  //     "></embed></body></html>"
  // );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default ExportTransaksiPenjualanMarketPlace;
